  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

.home,
.about-us,
.location,
.patient-forms {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}


.about-us {
  background-image: url(/static/media/IMG-0178.d2daa9a4.jpg);
  background-position: center;
